import { faAngleLeft, faAngleRight } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import classNames from 'classnames';
import { Link } from "gatsby";
import React, { FunctionComponent } from 'react';

export type PaginationProps = {
    hasPreviousPage: boolean
    hasNextPage: boolean
    currentPage: number;
    totalCount: number;
    perPage: number
}

export const Pagination: FunctionComponent<PaginationProps> = (props: PaginationProps): any => {
    const totalPage: number = Math.ceil(props.totalCount / props.perPage);

    const goToPage: any = (targetPage: number): any => {
        if ( targetPage === 1 ) {
            return "/"
        }

        return `/page/${ targetPage }`;
    }


    return <nav className={ 'text-center' }>
        <ul className={ 'list-inline' }>
            <li className={ classNames('list-inline-item', 'float-sm-start') }>
                {
                    props.hasPreviousPage ?
                    <Link className={ classNames('u-pagination-v1__item',
                                                 'g-brd-around',
                                                 'g-color-purple',
                                                 'g-brd-gray-light-v3',
                                                 'g-bg-purple--hover',
                                                 'g-brd-purple--hover',
                                                 'g-color-white--hover',
                                                 'g-rounded-50',
                                                 'g-pa-7-16') }
                          to={ goToPage(props.currentPage - 1) }>
                        <span aria-hidden="true">
                            <FontAwesomeIcon className={ 'mr2' } icon={ faAngleLeft }/>
                            Previous
                        </span>
                    </Link> : ""
                }
            </li>
            <li className={ classNames('list-inline-item', 'font-italic') }>
                <a className={ classNames('g-color-purple', 'g-pa-7-16') }>{ props.currentPage } of { totalPage }</a>
            </li>
            <li className={ classNames('list-inline-item', 'float-sm-end') }>
                {
                    props.hasNextPage ? <Link className={ classNames(
                        'u-pagination-v1__item',
                        'g-brd-around',
                        'g-color-purple',
                        'g-brd-gray-light-v3',
                        'g-bg-purple--hover',
                        'g-color-white--hover',
                        'g-rounded-50',
                        'g-pa-7-16') }
                                              to={ goToPage(props.currentPage + 1) }>
                    <span aria-hidden="true">
							Next
							<FontAwesomeIcon className={ 'ml2' } icon={ faAngleRight }/>
                        </span>
                    </Link> : ""
                }


            </li>
        </ul>
    </nav>
};
