import { graphql } from "gatsby";
import PropTypes from 'prop-types'
import React from "react"
import Article from "../component/article";
import Layout from "../component/layout";
import { Pagination } from "../component/pagination";

const Index = (props: any) => {
    const {
        allMdx: { pageInfo },
        my
    } = props.data;

    return <Layout title={''}>
        <div className={ 'g-pl-20--lg' }>
            {
                props.data.allMdx.edges.map(({ node }: any) => {
                    return <Article
                        key={ node.id }
                        id={ node.id }
                        title={ node.frontmatter.title }
                        date={ node.frontmatter.date }
                        description={ node.frontmatter.description }
                        path={ node.fields.path }
                        tags={ node.frontmatter.tags }
                        image={ my }/>
                })
            }
        </div>

        <Pagination { ...pageInfo }  />
    </Layout>
}

Index.propTypes = {
    data: PropTypes.object.isRequired,
    pageContext: PropTypes.shape({
                                     nextPagePath: PropTypes.string,
                                     previousPagePath: PropTypes.string,
                                 }),
}

export const postsQuery = graphql`
    query($limit: Int! = 10, $skip: Int! = 0) {
        my: imageSharp(fluid: {originalName: {eq: "myimage.jpg"}}) {
            gatsbyImageData(width: 20)
        }
        allMdx(
            filter: {fileAbsolutePath: {regex: "//posts//"}, internal: {type: {eq: "Mdx"}}, frontmatter: {published: {eq: true}}},
            sort: {fields: frontmatter___date, order: DESC},
            skip: $skip,
            limit: $limit
        ) {
            edges {
                node {
                    id
                    frontmatter {
                        title
                        description
                        date(formatString: "DD MMM YYYY")
                    }
                    fields {
                        path
                    }
                }
            }
            pageInfo {
                currentPage
                totalCount
                perPage
                hasPreviousPage
                hasNextPage
            }
        }
    }
`

export default Index;
