import { faBookOpen } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import classNames from "classnames";
import { CommentCount } from "disqus-react";
import { Link} from "gatsby";
import { GatsbyImage,getImage } from "gatsby-plugin-image";
import PropTypes from 'prop-types'
import React from "react";

const Article: React.FunctionComponent<any> = (props: any) => {
    return <article className={ "g-mb-100" }>
        <div className={ "g-mb-30" }>
            <span
                className={ classNames("d-block", "g-color-gray-dark-v4", "g-font-weight-700", "g-font-size-12", "text-uppercase", "mb-2") }>
                { props.date }
            </span>
            <h2 className={ classNames("h4", "g-color-black", "g-front-weight-600", "mb-3") }>
                <Link className={ classNames("u-link-v5", "g-color-black", "g-color-purple--hover") }
                      to={ `/posts${ props.path }` }>
                    { props.title }
                </Link>
            </h2>
            <p className={ classNames("g-color-gray-dark-v4", "g-line-height-18") }>
                { props.description }
            </p>
        </div>

        <ul className={ classNames("list-inline", "g-brd-y", "g-brd-gray-light-v3", "g-font-size-13", "g-py-13", "mb-0", 'd-flex', 'justify-content-between', 'align-items-center') }>
            <li className={ classNames("list-inline-item", "g-color-gray-dark-v4", 'g-hidden-sm-down') }>
                  <span className={ classNames("d-line-block",
                                               "g-color-gray-dark-v4",
                                               "g-px-15",
                                               "g-py-5") }>
                      <GatsbyImage className={ classNames("g-width-20",
                                                  "g-height-20",
                                                  "rounded-circle",
                                                  "g-mr-2") }
                           image={ getImage(props.image) } alt="Bruno Jan"/>
                      Bruno Jan
                  </span>
            </li>
            <li className={ classNames("list-inline-item", "g-color-gray-dark-v4", "float-right") }>
                <span className={ classNames("d-inline-block",
                                             "g-color-gray-dark-v4",
                                             "g-px-15",
                                             "g-py-5") }>
                    <CommentCount
                        shortname={ "yi-shiuan" }
                        config={
                            {
                                url: `https://yi-shiuan.github.io${ props.path }`,
                                identifier: props.id,
                                title: props.title,
                            }
                        }>
                    Comments
                </CommentCount>
                </span>
                <Link className={ classNames("d-inline-block",
                                             "g-color-gray-dark-v4",
                                             "g-color-white--hover",
                                             "g-bg-purple--hover",
                                             "rounded",
                                             "g-transition-0_3",
                                             "g-text-underline--none--hover",
                                             "g-px-15",
                                             "g-py-5") }
                      to={ `/posts${ props.path }` }>
                    <FontAwesomeIcon width={ 15.75 }
                                     height={ 12.25 }
                                     className={ classNames("align-middle", "g-font-size-default", "g-mr-2") }
                                     icon={ faBookOpen }/>
                    Read more
                </Link>

            </li>


        </ul>
    </article>
}

Article.propTypes = {
    id: PropTypes.string.isRequired,
    title: PropTypes.string.isRequired,
    description: PropTypes.string,
    date: PropTypes.string,
    path: PropTypes.string,
    tags: PropTypes.arrayOf(PropTypes.string),
    image: PropTypes.any
}

export default Article;
